import React, { useState } from "react";

export default function TextForm(props) {
  const [copySuccess, setCopySuccess] = useState("");
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("Converted to Uppercase !!", "success");
  };

  const handleLoClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("Converted to Lowercase !!", "success");
  };

  const handleClearClick = () => {
    let newText = "";
    setText(newText);
    props.showAlert("Text has been cleared !!", "success");
  };

  const handleExtraSpaces = () => {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.showAlert("Extra spaces have been removed !!", "success");
  };

  const handleCopyClick = () => {
    navigator.clipboard.writeText(text)
      .then(() => {
        setTimeout(() => setCopySuccess(""), 3500); // Clear message after 2 seconds
      })
      .catch((err) => {
        setTimeout(() => setCopySuccess(""), 3500);
      });
    props.showAlert("Copied to clipboard !!", "success");
  };

  const handleOnChange = (event) => {
    setText(event.target.value);
  };

  const [text, setText] = useState("");
  const wordCount = text.trim().split(/\s+/).filter((word) => word !== "").length;

  // Dynamic styles for TextForm
  const textFormStyle = {
    backgroundColor: props.customColors.textboxColor || (props.mode === 'dark' ? 'gray' : 'white'),
    color: props.customColors.fontColor || (props.mode === 'dark' ? 'white' : '#02022b')
  };

  return (
    <>
      <div className="container" style={{ color: textFormStyle.color }}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea
            className="form-control"
            value={text}
            onChange={handleOnChange}
            id="myBox"
            rows="8"
            style={{ backgroundColor: textFormStyle.backgroundColor, color: textFormStyle.color }}
          />
        </div>
        <button className="btn btn-primary mx-2" onClick={handleUpClick}>
          Convert to Uppercase
        </button>
        <button className="btn btn-primary mx-2" onClick={handleLoClick}>
          Convert to Lowercase
        </button>
        <button className="btn btn-primary mx-2" onClick={handleExtraSpaces}>
          Remove Extra Spaces
        </button>
        <button className="btn btn-primary mx-2" onClick={handleCopyClick}>
          Copy Text
        </button>
        <button className="btn btn-primary mx-2" onClick={handleClearClick}>
          Clear Text
        </button>
        {copySuccess && <div className="alert alert-success mt-3">{copySuccess}</div>}
      </div>
      <div className="container my-3" style={{ color: textFormStyle.color }}>
        <h2>Text Summary</h2>
        <p>{wordCount} words and {text.length} characters</p>
        <p>{0.008 * wordCount} Minutes read</p>

        <h2>Preview of your text :</h2>
        <p>{text.length > 0 ? text : "Enter text in the textbox to preview it here"}</p>
      </div>
    </>
  );
}
