import React, { useState } from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'

export default function Navbar(props) {
  const [selectedPart, setSelectedPart] = useState('');

  const handleSelectChange = (e) => {
    setSelectedPart(e.target.value);
  };

  const handleColorInputChange = (e) => {
    if (selectedPart) {
      props.handleColorChange(selectedPart, e.target.value);
      if (selectedPart === 'backgroundColor') {
        document.body.style.backgroundColor = e.target.value;
      }
    }
  };

  // Dynamic styles for navbar
  const navbarStyle = {
    backgroundColor: props.customColors.navbarColor || (props.mode === 'dark' ? '#343a40' : 'white'),
    color: props.customColors.fontColor || (props.mode === 'dark' ? 'white' : 'black'),
  };

  return (
    <nav className="navbar navbar-expand-lg" style={navbarStyle}>
      <div className="container-fluid">
        <a className="navbar-brand" href="#" style={{ color: navbarStyle.color }}>
          {props.title}
        </a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="#" style={{ color: navbarStyle.color }}>
                Home
              </a>
            </li>
            {/* <li className="nav-item">
              <Link className="nav-link" to="/about" style={{ color: navbarStyle.color }}>
                {props.aboutText}
              </Link>
            </li> */}
          </ul>

          {/* Custom Color Selector */}
          <div className="d-flex align-items-center">
            <select className="form-select mx-2" onChange={handleSelectChange} style={{ width: "210px" }}>
              <option value="">Customize Website</option>
              <option value="backgroundColor">Background Color</option>
              <option value="navbarColor">Navbar Color</option>
              <option value="textboxColor">Textbox Color</option>
              <option value="fontColor">Font Color</option>
            </select>

            {selectedPart && (
              <input
                type="color"
                onChange={handleColorInputChange}
                style={{ height: "38px", width: "50px", border: "none" }}
              />
            )}
          </div>

          {/* Dark Mode Toggle */}
          <div className={`form-check form-switch text-${props.mode === 'light' ? 'dark' : 'light'} mx-4`}>
            <input className="form-check-input" onClick={props.toggleMode} type="checkbox" role="switch" id="switchCheckDefault" />
            <label className="form-check-label" htmlFor="switchCheckDefault">Enable Darkmode</label>
          </div>
        </div>
      </div>
    </nav>
  )
}

Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  aboutText: PropTypes.string.isRequired
};

Navbar.defaultProps = {
  title: 'Set Title Here',
  aboutText: 'About'
};

